export const createStepData = (data) => {
    return Array.from(data).map((item, i, arr) => {
        if (item.close) {
            return {
                ...item,
                status: "finish",
            };
        }
        if (!item.close && !arr[i - 1]) {
            return {
                ...item,
                status: "process",
            };
        }
        if (arr[i - 1]["close"]) {
            return {
                ...item,
                status: "process",
            };
        }
        if (arr[i - 1].status === "process" || arr[i - 1].status === "wait") {
            return {
                ...item,
                status: "wait",
            };
        }
        return {
            ...item,
            status: "wait",
        };
    });
};